.program {
    background-color: var(--primary-bg-color);
}

.program__article {
    margin-bottom: 20px;
}

.article__title {
    margin-bottom: 8px;

    @media screen and (min-width: 768px) {
       margin-bottom: 16px; 
    }

    @media screen and (min-width: 1280px) {
        margin-bottom: 20px; 
     }
}

.article__description {
    margin-bottom: 8px;
}

.article__description--last {
    margin-bottom: 16px;
}

.article__list {
    margin-bottom: 8px;
  

}

.article__text:not(:last-child) {
margin-bottom: 8px;
}

.picture-box__list {
    display: flex;
    flex-wrap: wrap;
    row-gap: 60px;
    column-gap: 20px;
}

.picture-box__item {
    width: 130px;
    height: 130px;

    padding-top: 50px;
    padding-bottom: 10px;
    position: relative;
    
    border-radius: 50%;
    background-color: var(--main-clr);

    box-shadow: 0px 4px 4px rgba(171, 129, 108, 0.25);

    @media screen and (min-width: 768px) {
        width: 152px;
        height: 152px;
    }
    
    @media screen and (min-width: 1280px) {
        width: 180px;
        height: 180px;
    }
    
}

.picture-box__item::before {
    content: "";
    display: block;
    width: 80px;
    height: 80px;
    top: 0;
    left: 0;

    transform: translate(25%, -50%);

    position: absolute;
    background-image: url(../images/mobile/programm/mobile-1-cooking-donut.png);
    background-repeat: no-repeat;
    background-size: cover; 
    outline: 1px solid red;
    }

.picture-box__title {
    margin-bottom: 4px;

    text-align: center;
    font-family: var(--title-fnt);
    font-size: 18px;

    color: var(--secondary-txt-color);

    @media screen and (min-width: 1280px) {
        font-size: 24px;;
    }

}

.picture-box__description {
    margin-bottom: 8px;

    text-align: center;

    font-size: 14px;
    line-height: 1.22;
}

.picture__thumb {
    display: flex;
    justify-content: center;
}



